import { bindable, containerless } from 'aurelia-framework';

@containerless
export class EmBlogContent {

    @bindable blog;

    blogChanged(newValue, oldValue) {

    }

    /**
     * 
     */
    constructor() {
        this.subscribe = ea.subscribe(nsCons.EVENT_BLOG_SWITCH, (payload) => {
            this.getBlog();
        });
    }

    /**
     * 
     */
    unbind() {
        this.subscribe.dispose();
    }

    /**
     * DOM
     */
    attached() {
        this.getBlog();
    }

    getBlog() {
        if (!nsCtx.blogId) {
            return;
        }
        $.get('/admin/blog/get', {
            id: nsCtx.blogId
        }, (data) => {
            if (data.success) {
                this.blog = data.data;
            }
        });
    }
}
